<template>
<div class="d-flex flex-column date-input-wrapper" :class="wrapperClass">
    <label class="text-input-label">{{label}}<span class="text-danger">{{isRequired ? ' *':''}}</span></label>
    <span class="scope-1">
        <input :type="'text'" class="text-input" @change="changeDate" :value="date" :placeholder="placeholder">
        <span class="scope-2">
            <datetime v-model="selectDate" type="date"></datetime>
        </span>
    </span>
    <div class="text-danger error">{{error}}</div>
</div>
</template>

<script>
import moment from 'moment'

export default {
    name: 'DateTimeInput',
    props: {
        label: String,
        value: String,
        type: String,
        placeholder: {
            type: String,
            default: '',
        },
        error: String,
        wrapperClass: String,
        isPassword: {
            type: Boolean,
            default: false
        },
        isRequired: {
            type: Boolean,
        },
        changeDob: {
            type: Function,
            default: () => {}
        }
    },
    data: function () {
        return {
            date: '',
            selectDate: ''
        }
    },
    model: {
        event: 'change'
    },
    watch: {
        selectDate: function (value) {
            this.date = moment(value).format("YYYY.MM.DD");
            this.changeDob(moment(value).format("YYYYMMDD"))
        }
    },
    mounted: function () {
        if (this.value) {
            this.date = moment(this.value, 'YYYYMMDD').format("YYYY.MM.DD");
        }
    },
    methods: {
        changeDate(event) {
            this.changeDob(moment(event.target.value).format("YYYYMMDD"));
        }
    }
}
</script>

<style lang="scss">
@import '@/assets/styles/variables.scss';

.date-input-wrapper {
    width: 100%;

    .text-input-label {
        font-family: NotoSansKR;
        font-weight: 300;
        font-size: 1.4rem;
        line-height: 2rem;
        color: #7C848D;
        margin-bottom: .7rem;
    }

    .text-input {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: normal;
        font-size: 1.4rem;
        line-height: 2.0rem;
        color: #4f4f4f;
        outline: none;
        border: none;
        border-bottom: 0.05rem solid #E0E0E0;
        padding-bottom: .5rem;
        width: 100%;

        &:focus {
            color: $cy-blue;
            border-bottom: 0.05rem solid $cy-blue;
        }

        &::placeholder {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: #4F4F4F;
            opacity: 0.5;
        }
    }

    .error {
        display: inline-block;
        font-size: 1.2rem;
        line-height: 2.0rem;
        width: 100%;
        margin-bottom: .3rem;
        min-height: 2.0rem;
        position: relative;
    }

    .scope-1 {
        position: relative;
    }

    .scope-2 {
        position: absolute;
        right: .0rem;
        height: 1.8rem;
        width: 1.8rem;
        top: 0;
        overflow: hidden;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            background-position: center;
            background-image: url(../../../assets/images/calendar.png);
            background-size: cover;
            background-repeat: no-repeat;
            height: 1.8rem;
            width: 1.8rem;
        }
    }

    .vdatetime {
        position: absolute;
        top: 0;
        right: 0;
        height: 1.8rem;
        background: transparent;

        input {
            opacity: 0;
        }
    }
}
</style>
